.logo {
  @include flex($fd: column);
  position: relative;
  margin-bottom: 20px;

  @media (min-width: 767.98px) {
    flex-direction: row;
  }

  &__img {
    width: 250px;
    height: auto;

    @media (min-width: 767.98px) {
      position: absolute;
      top: -20px;
      left: 169px;
      width: 110px;

      animation: spin 5s linear infinite;
      transform-style: preserve-3d;
      perspective: 1000px;
      transform: rotateY(0deg);
    }

    @media (min-width: 1199.98px) {
      top: -20px;
      left: -25px;
    }
  }

  &__team-name {
    font-family: 'VirtualRave', sans-serif;
    letter-spacing: 0.07em;
    font-size: 42px;
    overflow: hidden;
    background: linear-gradient(to right, #ffffff, #c73920);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    @media (min-width: 1199.98px) {
      font-size: 36px;
      margin-left: 75px;
    }
  }
}

@keyframes spin {
  100% {
    transform: rotateY(360deg);
  }
}

.logo__team-wrap {
  position: relative;
}

.santa_hut {
  position: absolute;
  top: -6px;
  left: -38px;
  width: 65px;
  height: 65px;

  @media (min-width: 1199.98px) {
    top: -7px;
    left: 40px;

    width: 60px;
    height: 60px;
  }
}
